@import "ScssHelpers/_helpers_all.scss";

.our_products {
	background-color: var(--light_blue);

	@include bp($point_2, min) {
		padding-top: 19.3rem;
		padding-bottom: 17.8rem;
	}

	@include bp($point_3, $point_4) {
		padding-top: 7.9rem;
		padding-bottom: 7.1rem;
	}

	@include bp($point_4 - 1) {
		padding-top: 5.9rem;
		padding-bottom: 4.6rem;
	}
}

.our_products__head {
	@include flex_block(row, wrap, flex-start, center, center);

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 10.2rem;
		}
	}

	@include bp($point_3, $point_4) {
		&:not(:last-child) {
			margin-bottom: 8.1rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 3.2rem;
		}
	}
}

.our_products_title {
	@extend %h1;

	color: var(--primary_dark_color);

	@include bp($point_2, min) {
		margin-right: 2rem;
	}
}

.our_products_title__dott {
	display: inline-block;

	font-family: "Arial";
	color: var(--dot-color);
}

.our_products__slider {
	@include bp($point_3) {
		overflow: visible;
	}

	@include bp($point_4, min) {
		margin: 0 -2.6rem;
	}

	@include bp($point_3, $point_4) {
		&:not(:last-child) {
			margin-bottom: 6.9rem;
		}
	}

	@include bp($point_4 - 1) {
		margin: 0 -1.1rem;

		&:not(:last-child) {
			margin-bottom: 3.5rem;
		}
	}
}

.our_products__slider.is-destroy .swiper-wrapper {
	@include bp($point_2, min) {
		margin: 0 -2.6rem;
	}
}

.our_products_slide {
	&:nth-child(3n + 1) {
		.our_products_slide_image__hover {
			background-color: var(--orange_color);
		}
	}

	&:nth-child(3n + 2) {
		.our_products_slide_image__hover {
			background-color: var(--green_color);
		}
	}

	&:nth-child(3n + 3) {
		.our_products_slide_image__hover {
			background-color: var(--blue_color_2);
		}
	}

	@include bp($point_2, min) {
		height: auto;

		@include flex_block(column, nowrap, flex-start, flex-start, normal);
	}

	@include bp($point_4, min) {
		padding: 0 2.6rem;
	}

	@include bp($point_4 - 1) {
		padding: 0 1.1rem;
	}
	.our_products__slider.is-destroy & {
		@include bp($point_2, min) {
			width: 33.33% !important;
		}
		@include bp($point_4 - 1) {
			width: 100% !important;
		}
	}
}

.our_products_slide_image {
	position: relative;

	overflow: hidden;
	display: block;
	width: 100%;

	&:hover {
		@include bp($point_2, min) {
			.our_products_slide_image__hover {
				transform: translate(0, 0);
			}

			.our_products_slide_image_decor--mod_1 {
				&::before,
				&::after {
					opacity: 0.5;
				}
			}

			.our_products_slide_image_decor--mod_2 {
				&::before,
				&::after {
					opacity: 0.15;
				}
			}

			.our_products_slide_image_decor--mod_3 {
				&::before {
					opacity: 1;
				}

				&::after {
					opacity: 0.15;
				}
			}
		}
	}

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 3.8rem;
		}
	}

	@include bp($point_4, min) {
		padding-bottom: 77.7%;
	}

	@include bp($point_3, $point_4) {
		&:not(:last-child) {
			margin-bottom: 3.3rem;
		}
	}

	@include bp($point_4 - 1) {
		padding-bottom: 79%;

		&:not(:last-child) {
			margin-bottom: 2.7rem;
		}
	}
}

.our_products_slide_image__wrap {
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;
}

.our_products_slide_image__in {
	@extend %cover_img;
}

.our_products_slide_image__hover {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;

	width: 101%;
	height: 101%;

	transform: translate(-100%, -100%);

	@extend %transition_transform;
	will-change: transform;

	@include flex_block(row, nowrap, center, center, center);
}

.our_products_slide_image__text {
	color: var(--white_color);
	font-family: $biotif_font;
	font-size: 3rem;
	font-weight: 700;
}

.our_products_slide_image_decor {
	position: absolute;
	bottom: 0;
	right: 0;

	&::before,
	&::after {
		content: "";
		position: absolute;

		width: 3rem;
		height: 3rem;

		background-color: var(--white_color);

		opacity: 0;
		transition-property: opacity;
		transition-duration: 0.3s;
	}
}

.our_products_slide_image_decor--mod_1 {
	&::before {
		bottom: 0;
		right: 3rem;

		transition-delay: 0.15s;
	}

	&::after {
		bottom: 3rem;
		right: 0;

		transition-delay: 0.25s;
	}
}

.our_products_slide_image_decor--mod_2 {
	&::before {
		bottom: 3rem;
		right: 3rem;

		transition-delay: 0.35s;
	}

	&::after {
		bottom: 0;
		right: 6rem;

		transition-delay: 0.1s;
	}
}

.our_products_slide_image_decor--mod_3 {
	&::before {
		right: 0;
		bottom: 0;

		transition-delay: 0.45s;
	}

	&::after {
		right: 0;
		bottom: 6rem;

		transition-delay: 0.5s;
	}
}

.our_products_slide__wrap {
	@include bp($point_2, min) {
		flex: 1 1 auto;

		max-width: 46.8rem;

		@include flex_block(column, nowrap, flex-start, flex-start, normal);
	}

	@include bp($point_3) {
		max-width: 40.9rem;
	}
}

.our_products_slide__tag {
	color: var(--gray_color);
	font-family: $biotif_font;

	letter-spacing: -0.02em;
	line-height: 1.3;

	@include bp($point_2, min) {
		font-size: 1.8rem;

		&:not(:last-child) {
			margin-bottom: 1.7rem;
		}
	}

	@include bp($point_3, $point_4) {
		font-size: 1.4rem;

		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}

	@include bp($point_4 - 1) {
		font-size: 1.3rem;

		&:not(:last-child) {
			margin-bottom: 1.5rem;
		}
	}
}

.our_products_slide__link {
	display: block;

	color: var(--primary_dark_color);

	@extend %transition_color;

	&:hover {
		text-decoration: none;
		color: var(--primary_light_color);
	}
}

.our_products_slide__title {
	color: var(--primary_color);
	font-family: $biotif_font;
	font-weight: 600;
	line-height: 1.25;
	letter-spacing: -0.035em;

	@include bp($point_2, min) {
		font-size: 3.2rem;
		&:not(:last-child) {
			margin-bottom: 1.7rem;
		}
	}

	@include bp($point_3, $point_4) {
		font-size: 2.8rem;
		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}
	}

	@include bp($point_4 - 1) {
		font-size: 1.8rem;
		&:not(:last-child) {
			margin-bottom: 1.2rem;
		}
	}
}

.our_products_slide__descr {
	color: var(--gray_color);
	font-family: $biotif_font;

	@include bp($point_2, min) {
		flex: 1 1 auto;

		font-size: 2rem;
		line-height: 1.5;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}

	@include bp($point_3, $point_4) {
		font-size: 1.6rem;
		line-height: 1.375;

		&:not(:last-child) {
			margin-bottom: 2.1rem;
		}
	}

	@include bp($point_4 - 1) {
		font-size: 1.4rem;
		line-height: 1.35;

		&:not(:last-child) {
			margin-bottom: 1.3rem;
		}
	}
}

.our_products_slide_link {
	color: var(--blue_color_2);
	font-family: $biotif_font;
	letter-spacing: -0.02rem;
	line-height: 1.5;

	@extend %transition_color;
	&:hover,
	&:focus {
		text-decoration: none;
		color: var(--orange_color);
	}

	@include flex_block(row-reverse, nowrap, flex-end, center, center);

	@include bp($point_2, min) {
		font-size: 2rem;
	}

	@include bp($point_3, $point_4) {
		font-size: 1.8rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.4rem;
	}
}

.our_products_slide_link__icon {
	@include bp($point_2, min) {
		flex: 0 0 2.2rem;
		width: 2.2rem;
		height: 2.2rem;

		margin-right: 1.4rem;
	}

	@include bp($point_3, $point_4) {
		flex: 0 0 1.7rem;
		width: 1.7rem;
		height: 1.7rem;

		margin-right: 1.2rem;
		margin-top: 0.4rem;
	}

	@include bp($point_4 - 1) {
		flex: 0 0 1.6rem;
		width: 1.6rem;
		height: 1.6rem;

		margin-right: 1rem;
		margin-top: 0.2rem;
	}
}

// Progress bar
.our_products_bottom {
	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3) {
		@include flex_block(row, nowrap, flex-start, center, center);
	}
}

.our_products_bottom.hidden {
	display: none !important;
}

.our_products_bottom__progress {
	@extend %global_flex_block_row_nowrap_flex-start;

	flex-grow: 1;
	height: 0.2rem;

	background-color: var(--border_grey_color);

	.swiper-pagination-progressbar-fill {
		transform-origin: left top;
		flex-grow: 1;
		height: inherit;

		background-color: var(--blue_color);
	}

	@include bp($point_4, min) {
		margin: 0 1.6rem;
	}

	@include bp($point_4 - 1) {
		margin: 0 1.3rem;
	}
}

.our_products_bottom__value {
	font-family: $biotif_font;
	color: var(--blue_color);

	@include bp($point_4, min) {
		font-size: 1.6rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.3rem;
	}
}

// arrows
.our_products__navigation {
	@include bp($point_2, min) {
		margin-top: 3.2rem;
		margin-left: auto;

		@include flex_block(row, nowrap, flex-start, flex-start, center);
	}

	@include bp($point_3) {
		display: none;
	}
}

.our_products__navigation.hidden {
	display: none;
}

.our_products__arrow {
	width: 4.7rem;
	height: 4.7rem;
	border-radius: 50%;

	background-color: var(--blue_color);
	color: var(--white_color);
	cursor: pointer;

	&:not(:last-child) {
		margin-right: 2rem;
	}

	@extend %transition_color_background;

	&:hover,
	&:focus {
		background-color: var(--white_color);
		color: var(--primary_dark_color);
	}

	@include flex_block(row, nowrap, center, center, center);
}

.our_products__icon {
	width: 1rem;
	height: 1.5rem;

	.our_products__arrow--prev & {
		transform: rotate(-180deg);
		margin-left: -0.3rem;
	}

	.our_products__arrow--next & {
		margin-right: -0.3rem;
	}
}
